.graphic {
  container-type: inline-size;
}

.graphic__inner {
  display: flex;
  flex-direction: column;
  margin-bottom: var(--spacing-component);
  padding: 0;
}
@container (inline-size > 650px) {
  .graphic__inner {
    flex-direction: row;
  }
}

.graphic__image {
  flex: 1;
  margin: auto;
  border-radius: 20px;
  overflow: hidden;
}
.graphic__image img,
.graphic__image iframe {
  display: block;
  width: 100%;
  height: 100%;
  aspect-ratio: 16/9;
}

.graphic__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: var(--spacing-sm-2);
  flex: 1;
  padding-block: var(--spacing-sm-3);
}
@container (inline-size > 650px) {
  .graphic__content {
    padding: var(--spacing-sm-3);
  }
}

.graphic__title {
  margin: 0;
  font-family: var(--font-sans);
  font-size: 24px;
  font-weight: bold;
}

.graphic__body > :first-child {
  margin-top: 0;
}
.graphic__body > :last-child {
  margin-bottom: 0;
}